export default {
  name: 'carrousel',
  props: ['swiperSlides'],
  data () {
    return {
      swiperOption: {
        autoplay: {
          delay: 1000
        },
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  }
}



